body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div.arena {
  margin: auto;
  height: 500px;
  width: 500px;
  border: 10px solid black;
  position: relative;
}

div.head {
  position: absolute;
  height: 20px;
  width: 20px;
  background-color: blue;
  z-index: 1;
}

div.body {
  position: absolute;
  height: 20px;
  width: 20px;
  background-color: blue;
}

div.fruit {
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background-color: red;
}

div.info {
  margin: auto;
  align-self: center;
  font-size: 30px;
  color:green;
}

div.stats {
  position: relative;
  width: 300px;
  height: 300px;
  border: 4px solid black;
  z-index: 2;
  margin: 100px auto;
  opacity: 1;
  background-color: white;
  border-radius: 5px;
}

p.declareGameOver {
  color: red;
  font-size: 50px;
  margin : 50px auto 30px auto;
}

p.result {
  color: black;
  font-size: 20px;
  margin : 30px auto;
}


button.restart {
  width: 80px;
  height: 30px;
  background-color: lightgreen;
}

button.restart:active {
  background-color: green;
}

div.controls {
  display: flex;
  justify-content: center;
}

button.controlButton {
  font-size: 20px;
  background-color: lightblue;
  height: 80px;
  width: 80px;
  margin: 10px auto 20px auto;
  border-radius: 40px;
}